*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;

}
.Card-box{
    background: url(https://img.freepik.com/free-psd/3d-icon-weather-conditions-with-rain-sun_23-2150108737.jpg?size=626&ext=jpg&ga=GA1.1.2063888523.1705579820&semt=sph);
    min-height:   50vh;
    box-shadow:0px 1px 17px 8px black;;
    background-size: 100%;
     height: auto;
    text-align: center;

}
.img{
    width: 80px;
    margin: auto;
}

.navbar{
     
    
    font-size: 22px;
left: -100%;
position: relative;
background: transparent;
color: whitesmoke;
border-radius: 18px;
background: rgb(126, 126, 171);

transition: all 0.3s ease-in-out;
}
.acticeMenu{
     left: 0;
}
 

.navbar ul li {
    list-style: none;
    padding:  0px 20px;
}
 
